import React from 'react';
import {Link} from 'react-router-dom';
import './listing.css';

const ListingDisplay = (props) => {
    const renderList = ({restaurentList}) => {
        if(restaurentList){
            return restaurentList.map((item) => {
                return(
                    <div className="firstimg">
                        <div className="maindiv">
                            <div className="image">
                                <img src={item.thumb}
                                className="imgClass"/>
                            </div>
                            <div className="details">
                                <h1><Link to={`/details/${item._id}`}>
                                    {item.name}
                                </Link></h1>
                                <p>Cost Per Two Rs.{item.cost}</p>
                                <p>{item.city_name}</p>
                            </div>
                        </div>
                        <hr/>
                        <div className="moredetails">
                            <h2 style={{marginLeft:'2%'}}>Address</h2>
                            <p style={{marginLeft:'2%'}}>{item.locality}</p>
                            <p style={{marginLeft:'2%'}}>{item.address}</p>
                        </div>
                    </div>
                )
            })
            
        }else
        {
            return(
                <div>
                    <img src="/images/loader.gif"/>
                </div>
            )
        }
    }

    return(
        <>
        {renderList(props)}
        </>
    )
}

export default ListingDisplay